import weCropper from '../../../static/js/weCropper.js';
const device = uni.getSystemInfoSync();
const width = device.windowWidth;
const height = device.windowHeight - 50;
console.log(device);
 import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
export default {
	data() {
		return {
			cropperOpt: {
				id: 'cropper',
				width: width,
				height: height,
				scale: 2.5,
				zoom: 8,
				cut: {
					x: (width - 295) / 2,
					y: (height - 413) / 2,
					width: 295,
					height: 295
				}
			},
			weCropper: ''
		};
	},
     computed: mapState(['hasLogin','userInfo']), 
	methods: {
			...mapMutations(['setUserInfo']),
		/* back() {
			uni.redirectTo({
				url: '../infoDetail/infoDetail'
			});
		}, */
		touchStart(e) {
			this.weCropper.touchStart(e);
		},
		touchMove(e) {
			this.weCropper.touchMove(e);
		},
		touchEnd(e) {
			this.weCropper.touchEnd(e);
		},
		convertBase64UrlToBlob(dataURI, type) {
			var binary = atob(dataURI.split(',')[1]);
			var array = [];
			for (var i = 0; i < binary.length; i++) {
				array.push(binary.charCodeAt(i));
			}
			return new Blob([new Uint8Array(array)], { type: type }, { filename: '1111.jpg' });
		},
		blobToDataURL(blob) {
			var a = new FileReader();
			a.readAsDataURL(blob); //读取文件保存在result中
			a.onload = function(e) {
				var getRes = e.target.result; //读取的结果在result中
				console.log(getRes);
			};
		},
		getCropperImage() {
			let _this = this;
			uni.showLoading({
				title: '图片处理中...'
			});
			_this.weCropper.getCropperImage(avatar => {
				if (avatar) {
					//  获取到裁剪后的图片 
					//下面是上传到服务器的方法
					_this.$Request.upLoadFile('member_account/ajax_update_avatar',avatar).then(res => {
						console.log(res);
						console.log('===upload======');
						if (res.code == 200) {
							console.log('===upload=2=====');
							uni.hideLoading();
							console.log(res);
							
							let user_info = _this.userInfo;
							user_info.avatar = res.datas.avatar+'&rd='+Math.random();
							_this.setUserInfo(user_info);
							
								uni.showToast({
									icon: 'none',
									title:res.datas.info,
									duration:3000
								});
								wx.redirectTo({
									  //url: 'userpic?avatar=' + res.datas.avatar+'&rd='+Math.random()
									  url: 'userset'
									})
							//this.login(res.datas);
							//this.memberInfo = res.datas.member_info;
						}else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.hideLoading();
						}
					});
				/* 	uni.uploadFile({
						url: pathurl,
						filePath: avatar,
						name: 'file',
						formData: { token: token, userId: userId},
						success: res => {
							console.log('uploadImage success, res is:', res);
								uni.showToast({
								title: '上传成功',
								icon: 'success',
								duration: 1000
							});
						},
						ail: err => {
							console.log('uploadImage fail', err);
							uni.showModal({
								content: err.errMsg,
								showCancel: false
							});
							uni.hideLoading();
						},
						complete: () => {
							console.log('complate');
						}
					}); */
				} else {
					console.log('获取图片失败，请稍后重试');
				}
			});
		},
		uploadTap() {
			const self = this;

			uni.chooseImage({
				count: 1, // 默认9
				sizeType: ['compressed'], // 可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
				success(res) {
					let src = res.tempFilePaths[0];
					//  获取裁剪图片资源后，给data添加src属性及其值

					self.weCropper.pushOrign(src);
				}
			});
		}
	},
	onLoad(option) {
		 if (!this.hasLogin) {  
			uni.navigateTo({
				url:'../login',
			})
		}
		// do something
		const cropperOpt = this.cropperOpt;
		const src = option.src;
		if (src) {
			Object.assign(cropperOpt, {
				src
			});

			this.weCropper = new weCropper(cropperOpt)
				.on('ready', function(ctx) {})
				.on('beforeImageLoad', ctx => {
					uni.showToast({
						title: '上传中',
						icon: 'loading',
						duration: 3000
					});
				})
				.on('imageLoad', ctx => {
					uni.hideToast();
				});
		}
	}
};
